body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f7fb;
  margin: 0;
  padding: 0;
}

.App {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  background: #ffffff;
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
}

.heading {
  text-align: center;
  margin-bottom: 20px;
}

.heading h2 {
  color: #4b5563;
  font-weight: 600;
}

.addTask .main {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.addTask input {
  flex: 1;
  padding: 10px;
  font-size: 16px;
  border: 2px solid #d1d5db;
  border-radius: 8px;
  outline: none;
}

button.ss {
  background-color: #6366f1;
  color: white;
  border: none;
  padding: 10px 14px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
}

button.ss:hover {
  background-color: #4f46e5;
}

.list {
  margin-top: 30px;
}

.todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #eef2ff;
  padding: 12px;
  margin-bottom: 10px;
  border-radius: 8px;
}

.todo.completed {
  text-decoration: line-through;
  color: #9ca3af;
  background: #f3f4f6;
}
.todo h1 {
  margin: 0;
  font-size: 18px;
  flex-grow: 1;
}